@media only screen and (max-width: 768px) { 
  .add-project-page{
    padding: 1.5rem !important; 
}
}

.add-project-page{
    padding: 4rem;
}

.add-project-form{
    max-width: 800px;
}

.form-title {
  line-height: 1.3;
  color: #31344b;
  text-align: center;
  font-weight: 100;
}

.dropzone-area {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
}

.form-input-neu:focus-visible,
.dropzone-area:focus-visible {
  outline: none;
}

.dropzone-container {
  margin: 2rem 0rem;
  text-align: center;
}

.file-list {
  list-style: inside;
  padding: 0px;
  text-align: left;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.preview-img {
  width: 80px;
  height: 80px;
  margin: 1rem;
  margin-left: 0rem;
}

.main-preview-img {
  width: 100%;
  height: 180px;
  margin: 1rem 0rem;
}
